<template>
  <div class="agent-page">
    <div class="container">
      <!-- Loading State -->
      <div v-if="loading" class="loading-container">
        <div class="loading-spinner"></div>
        <p>Loading agent data...</p>
      </div>

      <!-- Error State -->
      <div v-else-if="error" class="error-container">
        <i class="fas fa-exclamation-circle"></i>
        <h3>Error Loading Agent</h3>
        <p>{{ error }}</p>
        <router-link to="/ai-agents" class="back-button error">
          <i class="fas fa-arrow-left"></i> Back to Agents
        </router-link>
      </div>

      <!-- if agent main_agent -->
      <div v-else-if="route.params.agentTag === 'main_agent'" class="error-container system-agent">
        <i class="fas fa-cat"></i>
        <h3>BITCAT System AI Agent</h3>
        <p>This is the main system AI agent. Statistics are not available for system agents.</p>
        <router-link to="/ai-agents" class="back-button error">
          <i class="fas fa-arrow-left"></i> Back to Agents
        </router-link>
      </div>

      <!-- if agent not found -->
      <div v-else-if="!agent && !loading && !error" class="error-container">
        <i class="fas fa-robot"></i>
        <h3>Agent Not Found</h3>
        <p>The AI agent you're looking for doesn't exist or has been removed</p>
        <router-link to="/ai-agents" class="back-button error">
          <i class="fas fa-arrow-left"></i> Back to Agents
        </router-link>
      </div>

      <!-- Agent Data -->
      <div v-else-if="agent" class="agent-container">
        <div class="header-section">
          <router-link to="/ai-agents" class="back-button">
            <i class="fas fa-arrow-left"></i> Back to Agents
          </router-link>
          <h1>AI Agent Profile</h1>
        </div>

        <div class="agent-card">
          <div class="avatar-section">
            <div class="avatar-container">
              <div class="avatar-glow"></div>
              <img :src="prepareAvatarUrl(agent.avatar)" :alt="agent.name" class="avatar">
              <div class="status-badge" :class="{ 'online': agent.available }">
                {{ agent.available ? 'Online' : 'Offline' }}
              </div>
            </div>
          </div>

          <div class="info-section">
            <h2 class="agent-name">{{ agent.name }}</h2>
            <div class="tag">{{ agent.agentTag }}</div>
            <p class="description">{{ agent.description }}</p>

            <div class="stats-grid">
              <div class="stat-item">
                <i class="fas fa-user"></i>
                <span class="label">Creator</span>
                <span class="value"><router-link :to="`/player/${agent.userTag}`">{{ agent.userTag }}</router-link></span>
              </div>
              <div class="stat-item">
                <i class="fas fa-comments"></i>
                <span class="label">Chats with</span>
                <span class="value">{{ agent.chats}} <small>user(s)</small></span>
              </div>
              <div class="stat-item">
                <i class="fas fa-chart-line"></i>
                <span class="label">Used</span>
                <span class="value">{{ agent.used}} <small>time(s)</small></span>
              </div>
              <!-- <div class="stat-item">
                <i class="fas fa-image"></i>
                <span class="label">Images</span>
                <span class="value">{{ agent.withImage ? 'Enabled' : 'Disabled' }}</span>
              </div> -->
              <div class="stat-item">
                <i class="fas fa-calendar"></i>
                <span class="label">Created</span>
                <span class="value">{{ formatDate(agent.createdAt) }}</span>
              </div>
            </div>

            <div class="actions">
              <button class="action-button primary" @click="useAgent">
                <i class="fas fa-comments"></i>
                Chat with Agent
              </button>
              <router-link v-if="agent.userTag === store.getters.userTag" class="action-button" :to="`/ai-agent/update/${agent.agentTag}`">
                <i class="fas fa-edit"></i>
                Update Agent
              </router-link>
              <button 
                v-if="agent.userTag === store.getters.userTag"
                class="action-button" 
                @click="showDeleteModal = true"
              >
                <i class="fas fa-trash"></i>
                Delete Agent
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Модальное окно для подтверждения удаления -->
    <Transition name="fade">
      <div v-if="showDeleteModal" class="modal-overlay">
        <div class="modal-content delete-modal">
          <i class="fas fa-exclamation-triangle warning-icon"></i>
          <h3>Delete AI Agent?</h3>
          <p>Are you sure you want to delete this AI agent? This action cannot be undone.</p>
          <ul class="delete-consequences">
            <li><i class="fas fa-times-circle"></i> Agent will be permanently deleted</li>
            <li><i class="fas fa-times-circle"></i> All statistics will be lost</li>
            <li><i class="fas fa-times-circle"></i> All chats will stop working</li>
          </ul>
          <div class="modal-actions">
            <button 
              class="delete-button" 
              :disabled="isDeleting"
              @click="deleteAgent"
            >
              <i class="fas fa-trash"></i>
              {{ isDeleting ? 'Deleting...' : 'Delete' }}
            </button>
            <button 
              class="cancel-button"
              :disabled="isDeleting"
              @click="showDeleteModal = false"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </Transition>
  </div>
</template>

<script setup>
import { ref, onMounted, watch } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import axios from 'axios'

const route = useRoute()
const store = useStore()
const router = useRouter()

const agent = ref(null)
const loading = ref(true)
const error = ref(null)
const showDeleteModal = ref(false)
const isDeleting = ref(false)

const prepareAvatarUrl = (avatarNum) => {
  return `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${avatarNum}.png`
}

const formatDate = (dateString) => {
  const date = new Date(dateString)
  return date.toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  })
}

const useAgent = () => {
  // Set current agent and open chat
  store.commit('SELECT_AGENT', {
    name: agent.value.name,
    agentTag: agent.value.agentTag,
    description: agent.value.description,
    avatar: agent.value.avatar
  })



  // Update agent info in store
  store.commit('UPDATE_AGENT_INFO', {
    name: agent.value.name,
    agentTag: agent.value.agentTag,
    avatar: agent.value.avatar
  })
  
  // Сохраняем выбранного агента в localStorage
  localStorage.setItem('selectedAgent', JSON.stringify({
    name: agent.value.name,
    agentTag: agent.value.agentTag,
    // description: props.description,
    avatar: agent.value.avatar
  }))
  
  // Получаем историю чата для этого агента
  let chatHistory = JSON.parse(localStorage.getItem('chatHistory') || '{}')
  
  // Если нет истории для этого агента, создаем первое сообщение с описанием
  if (!chatHistory[agent.value.agentTag]) {
    chatHistory[agent.value.agentTag] = [{
      type: 'agent',
      content: agent.value.description,
      timestamp: new Date().toISOString()
    }]
    localStorage.setItem('chatHistory', JSON.stringify(chatHistory))
  }
  
  // Открываем чат с агентом
  store.commit('SET_CURRENT_AGENT', {
    name: agent.value.name,
    agentTag: agent.value.agentTag,
    // description: props.description,
    avatar: agent.value.avatar
  })
  
  // Открываем окно чата
  store.commit('TOGGLE_AGENTS_LIST', false)
  store.commit('TOGGLE_CHAT', true)
}

const deleteAgent = async () => {
  isDeleting.value = true
  try {
    await axios.post('/ai/agent-delete', { agentTag: route.params.agentTag })
    // Redirect to agents list
    router.push('/ai-agents')
  } catch (err) {
    error.value = err.response?.data?.error || 'Failed to delete agent'
  } finally {
    isDeleting.value = false
    showDeleteModal.value = false
  }
}

const fetchAgent = async () => {
  loading.value = true
  error.value = null
  
  try {
    const { data } = await axios.get(`/ai/agent?agentTag=${route.params.agentTag}`)
    agent.value = data
  } catch (err) {
    error.value = err.response?.data?.error || 'Failed to load agent data'
  } finally {
    loading.value = false
  }
}

// Watch route changes
watch(() => route.params.agentTag, () => {
  fetchAgent()
})

onMounted(() => {
  fetchAgent()
})
</script>

